<template>
    <layout :showBack="isEdit" :title="`${isEdit ? '编辑' : '新增'}项目信息`">
        <div class="edit-container">
            <span class="title">基本信息</span>
            <van-form @submit="onSubmit">
                <van-field v-model="pageData.name"
                    name="项目名"
                    label="项目名"
                    placeholder="请输入项目名"
                    :rules="rules.name"/>
                <van-field v-model="pageData.signTime"
                    name="签订日期"
                    label="签订日期"
                    clearable
                    readonly
                    placeholder="请选择签订日期"
                    :rules="rules.signTime">
                    <template #button>
                        <van-button size="small" @click="calendarShow = true" native-type="button" type="primary">选择日期</van-button>
                    </template>
                </van-field>
                <van-field v-model="address"
                    name="地市"
                    label="地市"
                    clearable
                    readonly
                    placeholder="请选择地市"
                    :rules="rules.address">
                    <template #button>
                        <van-button size="small" @click="cityShow = true" native-type="button" type="primary">选择地市</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.address"
                    name="详细地址"
                    label="详细地址"
                    placeholder="请输入详细地址"
                    :rules="rules.detailAddress"/>
                <van-field v-model="pageData.liableName"
                    name="负责人"
                    label="负责人"
                    clearable
                    readonly
                    placeholder="请选择负责人"
                    :rules="rules.liableName">
                    <template #button>
                        <van-button size="small" @click="userShow = true" native-type="button" type="primary">选择负责人</van-button>
                    </template>
                </van-field>
                <span class="title">业主信息</span>
                <van-field v-model="pageData.ownerName"
                    name="业主"
                    label="业主"
                    placeholder="请输入业主"
                    :rules="rules.ownerName"/>
                <van-field v-model="pageData.ownerAddress"
                    name="业主地址"
                    label="业主地址"
                    placeholder="请输入业主地址"
                    :rules="rules.ownerAddress"/>
                <van-field v-model="pageData.ownerPhone"
                    name="联系电话"
                    label="联系电话"
                    placeholder="请输入联系电话"
                    :rules="rules.ownerPhone"/>
                <span class="title">备注信息</span>
                <van-field v-model="pageData.remark"
                    name="备注"
                    label="备注"
                    type="textarea"
                    placeholder="请输入备注"/>
                <div class="btn">
                    <van-button round block type="primary" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>

        <van-calendar v-model="calendarShow" @confirm="calendarConfirm" />

        <van-popup v-model="cityShow" position="bottom" :style="{ height: '308px' }">
            <van-area title="标题"
                @cancel="cityShow = false"
                @confirm="cityConfirm"
                :area-list="areaList" />
        </van-popup>

        <van-popup v-model="userShow" position="bottom" :style="{ height: '100%' }">
            <div class="simple-list">
                <header>
                    <input type="text" v-model.lazy="userName" placeholder="输入需要过滤的名字"/>
                </header>
                <ul>
                    <li v-for="it in userList" v-show="it.show" :key="it.id">
                        <span>{{it.name}}</span>
                        <em>{{it.id}}</em>
                        <i @click="pickUser(it)"></i>
                    </li>
                </ul>
            </div>
        </van-popup>
    </layout>
</template>

<script>
    import { areaList } from '@vant/area-data'
    import { formatDate } from '../../util'
    import { userListApi } from '../../apis/UserApi'
import { projectAddApi, projectSaveApi } from '../../apis/ProjectApi'

    export default {
        data() {
            return {
                isEdit: false,
                pageData: {
                    id: '',
                    name: '',
                    liableId: '',
                    liableName: '',
                    signTime: '',
                    province: '',
                    city: '',
                    county: '',
                    address: '',
                    ownerId: '',
                    ownerName: '',
                    ownerAddress: '',
                    ownerPhone: '',
                    remark: ''
                },
                rules: {
                    name: [{required: true, message: '项目名称必须输入'}],
                    liableName: [{required: true, message: '负责人必须输入'}],
                    signTime: [{required: true, message: '签订事件必须输入'}],
                    address: [{required: true, message: '地址必须输入'}],
                    detailAddress: [{required: true, message: '详细地址必须输入'}],
                    ownerName: [{required: true, message: '业主必须输入'}],
                    ownerAddress: [{required: true, message: '业主地址必须输入'}],
                    ownerPhone: [{required: true, message: '业主电话必须输入'}]
                },
                calendarShow: false,
                address: '',
                cityShow: false,
                areaList,
                userList: [],
                userName: '',
                userShow: false
            }
        },
        watch: {
            userName(val) {
                this.userList = this.userList.map(it => {
                    it.show = it.name.includes(val)
                    return it
                })
            }
        },
        async created() {
            let query = this.$route.query,
                cache = this.$store.getters['common/getEditData']

            this.isEdit = !!query.id
            if (this.isEdit) {
                for (let name in this.pageData) {
                    this.pageData[name] = cache[name] || ''
                }

                this.pageData.signTime = formatDate(this.pageData.signTime, 'yyyy-MM-dd')
                this.address = this.pageData.province + '/' + this.pageData.city + '/' + this.pageData.county
            }

            let result = await userListApi({page: 1, size: 1000})
            if (result.code === 200) {
                this.userList = result.data.rows.map(it => {
                    it.show = true
                    return it
                })
            }
        },
        methods: {
            async onSubmit() {
                let _arr = this.address.split('/')
                this.pageData.province = _arr[0]
                this.pageData.city = _arr[1]
                this.pageData.county = _arr[2]

                let result
                if (this.isEdit) {
                    result = await projectSaveApi(this.pageData)
                } else {
                    result = await projectAddApi(this.pageData)
                }

                this.$toast.clear()

                if (result.code === 200) {
                    this.$toast.success(`${this.isEdit ? '修改' : '新增'}成功`)
                } else {
                    this.$toast.fail(`${this.isEdit ? '修改' : '新增'}成功`)
                }
            },
            calendarConfirm(date) {
                this.pageData.signTime = formatDate(date, 'yyyy-MM-dd')
                this.calendarShow = false
            },
            cityConfirm(data) {
                this.address = data.reduce((nl, v) => {
                    nl.push(v.name)
                    return nl
                }, []).join('/')
                this.cityShow = false
            },
            pickUser(row) {
                this.pageData.liableName = row.name
                this.pageData.liableId = row.id
                this.userShow = false
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/common.less";

    .edit-container {
        .title {
            display: block;
            font-size: .7rem;
            line-height: 42px;
            color: @font-8c;
        }
        display: block;
        padding: 12px 12px 0;
        .btn {
            margin-top: 16px;
        }
    }
</style>